<template>
  <div>
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item>人员管理</a-breadcrumb-item>
        <a-breadcrumb-item>管理员管理</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <div class="serch_field">
      <a-form-model
        layout="inline"
      >
        <a-form-model-item style="width: 100%;">
          <a-form-model-item>
            <a-input
              placeholder="请输入书籍名称、作者、编号..."
              v-model="params.content"
              style="width: 220px;"
            ></a-input>
          </a-form-model-item>
          <a-button type="primary">搜索</a-button>
        </a-form-model-item>

        <a-form-model-item>
          <a-form-model-item label="操作状态">
            <a-select
              v-model="params.status"
              :options="status"
              style="width: 150px;"
            ></a-select>
          </a-form-model-item>
        </a-form-model-item>

      </a-form-model>
    </div>

    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="gender" slot-scope="text">
        {{ text.gender === 1 ? '男' : '女'}}
      </template>
      <template slot="auth" slot-scope="text">
        {{ text.auth === 1 ? '正常' : '已禁用' }}
      </template>
      <template slot="status" slot-scope="text">
        {{ formatStatus(text.status)}}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="viewReader(text.id)">查看</a-button>
        <a-button type="link" @click="banReader(text.id)">禁用</a-button>
      </template>
    </s-table>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapActions, mapState } from 'vuex'
export default {
  components: {STable},
  data() {
    let params = {
      status: '',
      content: '',
      start_time: '',
      end_time: ''
    }
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '姓名',
        dataIndex: 'name'
      },
      {
        title: '性别',
        scopedSlots: { customRender: "gender"},
      },
      {
        title: '手机',
        dataIndex: 'phone'
      },
      {
        title: '用户状态',
        scopedSlots: { customRender: "status"},
      },
      {
        title: '用户权限',
        scopedSlots: { customRender: "auth"},
      },
      {
        title: '操作',
        width: '250px',
        scopedSlots: { customRender: "action"},
      },
    ]
    return {
      params,
      columns,
      loadData: paramters => {
        return new Promise(resolve => {
          this.getReaderRecord(Object.assign({}, this.params, paramters))
            .then(res => {
              resolve({
                list: res.data,
                total: res.total
              })
            })
        })
      }
    }
  },
  computed: {
    status() {
      return [
        {
          label: '全部',
          value: ''
        },
        {
          label: '借阅',
          value: 1
        },
        {
          label: '续借',
          value: 2
        },
        {
          label: '归还',
          value: 3
        },
        {
          label: '超时',
          value: 4
        },
      ]
    }
  },
  methods: {
    ...mapActions('reader', {
      getReaderRecord: 'getReaderRecord'
    })
  }
}
</script>